export default function tableCurrent() {
  if (!this.tables)
    return this.table;
  let table, zoom = parseInt(this.mapview.Map.getView().getZoom()), zoomKeys = Object.keys(this.tables), minZoomKey = parseInt(zoomKeys[0]), maxZoomKey = parseInt(zoomKeys[zoomKeys.length - 1]);
  table = this.tables[zoom];
  table = zoom < minZoomKey ? this.tables[minZoomKey] : table;
  table = zoom > maxZoomKey ? this.tables[maxZoomKey] : table;
  return table;
}
