const hooks = {
  current: {
    layers: [],
    locations: []
  },
  set,
  parse,
  remove,
  removeAll,
  push,
  filter
};
export default hooks;
function parse() {
  window.location.href.replace(/[?&]+([^=&]+)=([^&]*)/gi, (match, key, value) => {
    if (hooks.current[key]) {
      hooks.current[key] = decodeURI(value).split(",");
    } else {
      hooks.current[key] = decodeURI(value);
    }
  });
  if (hooks.current.token) {
    window.history.replaceState({}, document.title, window.location.href.replace(new RegExp(`token=${hooks.current.token}`), ""));
    delete hooks.current.token;
  }
}
function set(_hooks) {
  Object.assign(hooks.current, _hooks);
  pushState();
}
function remove(key) {
  if (Array.isArray(hooks.current[key])) {
    hooks.current[key].length = 0;
  } else {
    delete hooks.current[key];
  }
  pushState();
}
function removeAll() {
  Object.keys(hooks.current).forEach((key) => remove(key));
  pushState();
}
function push(key, val) {
  if (hooks.current[key]) {
    if (hooks.current[key].indexOf(val) < 0)
      hooks.current[key].push(val);
  } else {
    hooks.current[key] = [val];
  }
  pushState();
}
function filter(key, val) {
  hooks.current[key] = hooks.current[key].filter((el) => el !== val);
  pushState();
}
function pushState() {
  try {
    window.history.pushState({}, document.title, `?${mapp.utils.paramString(hooks.current)}`);
  } catch (me) {
    console.log(me);
  }
}
